import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';


@Component({
  selector: 'app-crud',
  templateUrl: './crud.page.html',
  styleUrls: ['./crud.page.scss'],
})
export class CrudPage implements OnInit {

  id: number;
  your_name: string="";
  gender: string="";
  date_birth: string="";
  email_address: string="";
  password: string="";
  confirm_pass: string="";

  disabledbutton;

  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.actRoute.params.subscribe((data: any)=>{
      console.log(data);
      this.id = data.id;

      if(this.id != 0){
        this.loadUser()
      }
    });
  }

  loadUser(){
    return new Promise(resolve =>{
      let body = {
        aksi: 'load_single_data', 
        id: this.id
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res: any) => {
        this.your_name = res.result.your_name;
        this.gender = res.result.gender;
        this.date_birth = res.result.date_birthday;
        this.email_address = res.result.email_address;
      });
    });
  }

  ionViewDidEnter(){
    this.disabledbutton = false;
  }
  

  async crudAction(a){
    if(this.your_name==""){
      this.presentToast('Tu nombre es requerido');
    }else if(this.gender==""){
      this.presentToast('El genero es requerico');
    } else if(this.date_birth==""){
      this.presentToast('Se requiere la Fecha de Nacimiento');
    } else if(this.email_address==""){
      this.presentToast('SE requiere el Correo');
    } else if(this.password=='' && this.id == 0){
      this.presentToast('Se requiere la Contraseña');
    }else{
      this.disabledbutton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Espere por favor....',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'proses_crud',
          id: this.id,
          your_name: this.your_name,
          gender: this.gender,
          date_birth: this.date_birth,
          email_address: this.email_address,
          password: this.password,
          action: a
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast(res.msg);
            this.router.navigate(['/home2']);
          }else{
            loader.dismiss();
            this.disabledbutton = false;
            this.presentAlert(res.msg, a);
          }
        },(err)=>{
          loader.dismiss();
          this.disabledbutton = false;
          this.presentAlert('Timeout',a);
        });
      });
    }
  }
  

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500,
    });
    toast.present();
  }

  async presentAlert(a,b) {
    const alert = await this.alertCtrl.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Close',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Try Again',
          handler: () => {
            this.crudAction(b);
          }
        }
      ]
    });

    await alert.present();
  }

}


 


  